import React from 'react';
import './carousel.css';
import img1 from '../images/shoes.jpg';
import img2 from "../images/shoes2.jpg";
import img3 from "../images/shoes3.jpg";
import img4 from "../images/shoes4.jpg";


// Import Swiper styles
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "swiper/css/effect-fade";

import { Autoplay, Pagination, Navigation, EffectFade } from "swiper";
import { Typography } from '@mui/material';


const Carousel = () => {
    return (
        <>
        <div id='contact'>
        <Typography variant='h3' align='center' sx={{mt:{lg:11, xs:23}, bottom:{xs:50, lg:10}, mb:{lg:2, xs:0}}} position='relative'>Newly Added</Typography>  
          <Swiper
        effect={'fade'}
        fadeEffect={true}
        loop={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Autoplay, Pagination, Navigation, EffectFade]}
        className="mySwiper"
      >
        <SwiperSlide><img src={img1}/></SwiperSlide>
        <SwiperSlide><img src={img2}/></SwiperSlide>
        <SwiperSlide><img src={img3}/></SwiperSlide>
        <SwiperSlide><img src={img4}/></SwiperSlide>
      </Swiper>
        </div>
        </>
    )
}

export default Carousel