import React, { useState } from 'react';
import './footer.css';
import { Typography, Grid, TextField} from '@mui/material';
import { Box, Container } from '@mui/system';
import FacebookRoundedIcon from '@mui/icons-material/FacebookRounded';
import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GoogleIcon from '@mui/icons-material/Google';


   

const Footer = () => {    
  const [value, setvalue] = useState('')
  const email = /^[A-Za-z._]{3,}@[A_Za-z]{3,}[.]{1}[A-Za-z.]{3,6}$/;

   
  return (
        <>     
        <div> 
        <Box sx={{backgroundColor:'black', mt:5}}>
            <Container>
            <Grid container>
                <Grid item lg={4} xs={12}>
                <Typography variant='h3' color='white' sx={{textAlign:{xs:'center', lg:'left'}}}>shoes</Typography> 
                </Grid>
                <Grid item lg={4} xs={12}>
                <Box sx={{mt:{xs:2, lg:0}, textAlign:'center'}}>
                <TextField 
                id="filled-search"
                label='Email'
                type='email'
                value={value}
                onChange={(e) => setvalue(e.target.value)}
                variant='standard'
                className='border'
                InputLabelProps={{className:'text'}}
                InputProps={{className:'color'}}
                error={value}
                helperText={email.test(value) ? <div style={{color:'white'}}>Successfully logged in</div> : <div style={{color:'red'}}>Please enter valid email</div> }
                Input
                />
                </Box>    
                </Grid>
                <Grid item lg={4} xs={12}>
                <Box color='white' sx={{mt:4, textAlign:'center'}}> 
                 <FacebookRoundedIcon/>
                    <LinkedInIcon sx={{pl:2}}/>
                    <InstagramIcon sx={{pl:2}}/>
                    <GoogleIcon sx={{pl:2}}/>
                 </Box>
                </Grid>
            </Grid>
            </Container>
         </Box>
        </div>   
        </>
    )
}

export default Footer;
