import React from 'react';
import StarIcon from '@mui/icons-material/Star';
import { Box } from '@mui/system';
import { Grid, Typography, CardMedia, Card, Divider } from '@mui/material';


const Cardmap = (props) => {
    return (
        <Grid item lg={6} xs={12} sm={12} href='#'>
        <Typography component="a"
          href="#" sx={{ textDecoration: 'none' }} color="black">
          <Box sx={{ width: { lg: 550 }, mx:'auto'}}>
            <Card sx={{ width: { lg: '100%' }, boxShadow: '0px 0px 10px 4px' }}>

              <CardMedia
                sx={{ height: 250 }}
                image={props.img}
              />

            </Card>
            <Grid container mt={4}>
              <Grid item lg={6} xs={6}>
                <Box color='yellow'>
                <StarIcon/>
                <StarIcon />
                <StarIcon />
                <StarIcon />
                </Box>
              </Grid>
              <Grid lg={6} xs={6}>
                <Typography variant='h5' align='right'>
                  {props.text}
                </Typography>
              </Grid>
            </Grid>
          </Box>
        </Typography>
        <Divider sx={{mt:3}}/>
      </Grid>
    )
}

export default Cardmap;