import { Typography } from '@mui/material';
import { Box } from '@mui/system';
import React, { useState } from 'react';
import Deal from '../deal/deal';
import Deal2 from '../Deal2/deal2';
import Deal3 from '../Deal3/deal3';
import './variety.css';




const Variety = () => {
   const [active, setactive] = useState('firstCard');

     const handleClick = () => {
      setactive('firstCard')
     } 

     const handleClick2 = () => {
      setactive('secondCard')
     }

     const handleClick3= () => {
      setactive('thirdCard')
     }
    //  onClick={handleClick2} className={active === 'secondCard' && 'purple'}
   
    //  purple

    return (
     <>
     <Box sx={{display:'flex', justifyContent:'center', mt:{lg:3}}}>
      <div onClick={handleClick}>
      <Typography component='a' variant='h5' className={`new ${active === 'firstCard' && 'black'}`}>New</Typography>
      </div>
      <Box onClick={handleClick2}>
      <Typography component='a' variant='h5' className={`new ${active === 'secondCard' && 'black'}`}>Best</Typography>
      </Box>
      <Box onClick={handleClick3}>
      <Typography component='a' variant='h5' className={`new ${active === 'thirdCard' && 'black'}`}>Sales</Typography>
      </Box>
     </Box>
     {active === 'firstCard' && <Deal/>}
     {active === 'secondCard' && <Deal2/>}
     {active === 'thirdCard' && <Deal3/>}
     </>          
    )
}


export default Variety