import React, { useState } from 'react';
import './card.css';
import { Box } from '@mui/system';
import { Grid, Typography } from '@mui/material';
import Cardmap from './cardmap';
import cardata from './cardata';

function createcard (data) {
  return (
    <Cardmap
      text={data.text}
      img={data.img}
    />
  )
}


const Cards = () => {

  const [animate, setanimate] = useState(false)  
  
  const changeAni = () => {
    if(window.scrollY >= 100){
      setanimate(true)
    }
    else {
      setanimate(false)
    }
  }

  window.addEventListener('scroll', changeAni)

const typo = {
 mt: {
  lg: 10,
  xs: 6
 }
}


const cardDesign = {
  margin: {
    xs:2
  },
  top: 60 
} ;


  return (
    <>
    <div  id='demo'>
    <div className='textanimate'>
      <Typography variant='h4' align='center' sx={typo}>
        TOP SELLING
      </Typography>
      </div>
      <div className={animate ? 'animates' : 'animator'}>
      <Box sx={cardDesign} position='relative'>
        <Grid container spacing={5}> 
          {
            cardata.map(createcard)
          }
        </Grid>
      </Box> 
      </div>
    </div>
    </>
  )
}

export default Cards;