import React, { useState } from "react";
import './navbar.css'
import { Box, Container } from "@mui/system";
import { AppBar, Grid, Typography } from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import CloseIcon from '@mui/icons-material/Close';
import {Link} from 'react-scroll'


const Navbar = () => {
   const [nav, openav] = useState(true)
   const [menu, setmenu] = useState(false)
   

   const handleclick =() =>{
      openav(!nav)
   }

   const closemenu = () => {
      openav(true)
   }

   

   const changebg = () => {
      if (window.scrollY >= 10) {
         setmenu(true)
      } else {
         setmenu(false)
      }
   }

   window.addEventListener("scroll", changebg)

   const sxStyles = {
      backgroundColor: 'transparent'
   }

   const sxStyler = {
      backgroundColor: 'white',
   }


  const active = true

     

    return (
        <>
       
        <Box sx={{m:0}}>
         
         <AppBar position="fixed" sx={menu ? sxStyler : sxStyles}>
         <Box sx={nav ? sxStyles : sxStyler }>
         <Container>
           <Grid container>
             <Grid item lg={4} xs={6} sm={6}>
                <Box sx={{display:{lg:"none", xs:"inline-block", sm:"none"}, pr:1}}>
                <div onClick={handleclick}>
                {nav ? <MenuIcon sx={{color:"black"}}/> : <CloseIcon sx={{color:"black"}}/>}
                </div> 
                </Box>
               
             <Typography
                 variant="h4"
                 component="a"
                 href="#"
                 color="black"
                 sx={{textDecoration:"none"}}
                 >
                    Shoes
                 </Typography>
                
             </Grid>
             <Grid item lg={4} xs={12} sm={6} sx={{display:{lg:"block", xs:"none", sm:"block"}}}>
               <Box align="center" sx={{pt:1}}>
               <Box sx={{display:'inline-block'}}>
                  <div className="nav-item">
                  <Link to="demo" activeClass="active" smooth={true} spy={true} offset={-80} duration={500} className="anc">Home</Link>
                  </div>
                  
                 </Box>
                 <Box sx={{display:'inline-block', pl:{lg:3}}}>
                 <div className="nav-item">
                  <Link to="about" activeClass="active" smooth={true} spy={true} offset={60} duration={500} className="anc">About</Link>
                  </div>
                 </Box>
                 <Box sx={{display:'inline-block', pl:{lg:3}}}>
                 <div className="nav-item">
                  <Link to="contact" activeClass="active" smooth={true} spy={true} offset={130} duration={500} className="anc">Contact</Link>
                  </div>
                 </Box>
               </Box>  
             </Grid>

                {/* mui icons */}

             <Grid item lg={4} xs={6}>
                <Box align="right" sx={{mt:1}}>
                  <SearchIcon style={{color:"black"}}/>
                  <ShoppingCartIcon sx={{pl:2, color:"black"}}/>
                  <AccountCircleIcon sx={{pl:3, color:"black"}}/>
                </Box>
                 
             </Grid>
            </Grid> 
            </Container>
            <Box sx={{display:{lg:"none"}}}>
            <div className={nav ? "none" : "flex"}>
              <div className="nav-item">
            <Link activeClass="active" smooth={true} spy={true} offset={-50} duration={500} to="demo" className="small" onClick={closemenu}>Home</Link>
             </div>    
                  {/* onClick={closemenu}
                sx={navmenu} */}
                <div className="nav-item mtop">
                   <Link activeClass="active" smooth={true} spy={true} offset={100} duration={500} to="about" className="small" onClick={closemenu}>About</Link>
               </div>
                  <div className="nav-item mtop"> 
                  <Link activeClass="active" smooth={true} spy={true} offset={90} duration={500} to="contact" className="small" onClick={closemenu}>Contact</Link>   
               </div>
            </div>  
            </Box>
            </Box>
         </AppBar>   
            
        </Box>
       
       
        </>
    )
}
export default Navbar;