import React from 'react';
import './deal2.css';
import { border, Box, Container } from '@mui/system';
import { Card, CardMedia, Grid, Typography } from '@mui/material';
import img1 from '../images/shoes2.jpg';

const Deal2 = () => {
    



    return (
     <>
     <Box sx={{mt:5, mx:{xs:2}}}>
      <Box className={'animatcard'}>
        <Grid container>
         <Grid item lg={6} xs={12}>
            <Typography component='a' href='#'>
              <Box sx={{width:{lg:550}, mx:'auto'}}>
                 <Card sx={{ width: { lg: '100%' }, boxShadow: '0px 0px 10px 4px', borderRadius:'20px' }}>
                   <CardMedia
                    sx={{ height:{lg:350, xs:250} }}
                    image={img1}
                   />   
                 </Card>       
              </Box>
            </Typography>
         </Grid>
         <Grid item lg={6} xs={12}>
            <Typography component='a' href='#'>
              <Box sx={{width:{lg:550}, mx:'auto', mt:{lg:0, xs:10}}}>
                 <Card sx={{ width: { lg: '100%' }, boxShadow: '0px 0px 10px 4px', borderRadius:'20px' }}>
                   <CardMedia
                    sx={{ height:{lg:350, xs:250} }}
                    image={img1}
                   />   
                 </Card>       
              </Box>
            </Typography>
         </Grid>
       </Grid>
      </Box>
      </Box>  
     </>          
    )
}

export default Deal2